import React, { useEffect, useState } from 'react'

function Notes() {
    const[contents, setContents] = useState([])

    useEffect(() => {
        fetch('http://localhost:8000/notes')
        .then(res => res.json())
        .then(data => setContents(data))
    }, [])
    return (
        <div>
            {contents.map(content => (
                <p key={content.id}>
                    {content.title}
                </p>
            ))}
        </div>

    )
}

export default Notes;